.videoBlock {
  width: 100%;
  height: 200px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  color: black;

  a {
    position: relative;
    width: 100%;
    height: 100%;
    text-decoration: none;
    color: black;
    .video__loading {
      position: absolute;
      top: 0;
      width: 100%;
      height: 100%;
      background-color: black;
      display: flex;
      justify-content: center;
      align-items: center;
      opacity: 0;
      span {
        font-size: 50px;
        color: white;
      }
    }
    video {
      background-color: $bgColor;
      width: 100%;
      height: 100%;
      outline: none;
    }
  }
  h4,
  h6 {
    display: flex;
    justify-content: center;
    margin-top: 5px;
  }
  h4 {
    font-size: 15px;
  }
  h6 {
    font-size: 12px;
  }
}
@media screen and (max-device-width: 480px) {
  .videoBlock {
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    color: black;

    a {
      width: 100%;
      height: 80%;
      text-decoration: none;
      color: black;
      video {
        background-color: $bgColor;
        width: 100%;
        height: 100%;
        outline: none;
      }
    }
    h4,
    h6 {
      display: flex;
      justify-content: center;
      margin-top: 5px;
    }
    h4 {
      font-size: 15px;
    }
    h6 {
      font-size: 12px;
    }
  }
}
